import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import routeConstant from 'routes/route/routeConstant';
import { Card, Row, Col } from 'antd';
import moment from 'moment';
import { TableData, ColEditDelete } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';
// import { getBonusNotes } from 'services/BNnote';
import { getListClassReward } from 'services/ClassReward';
import FormFilterReward from './FormFilterReward';

class ClassRewardPage extends Component {
  state = {
    reload: 0,
    paramSearchClass: {
      semesterId: null,
    },
  };
  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearchClass: getQueryParams() });
  };

  render() {
    const columns = [
      {
        title: 'Lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Số QD',
        dataIndex: 'approvalNumber',
        key: 'approvalNumber',
      },
      {
        title: 'Ngày QD',
        dataIndex: 'approvalDate',
        key: 'approvalDate',
        render: (text, record) => <div>{moment(record.approvalDate).format('DD-MM-YYYY')}</div>,
      },
      {
        title: 'Danh hiệu',
        dataIndex: 'gradeTitle',
        key: 'gradeTitle',
      },
      {
        title: 'Cấp khen thưởng',
        dataIndex: 'parentBonusTypeLabel',
        key: 'parentBonusTypeLabel',
      },
      {
        title: 'Hình thức',
        dataIndex: 'description',
        key: 'description',
      },
      {
        title: 'Loại khen thưởng',
        dataIndex: 'bonusTypeLabel',
        key: 'bonusTypeLabel',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.KHUYEN_THUONG__NHAP_QDKT_LOP__NHAP_DU_LIEU,
                search: `?id=${record.approvalId}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
            hidenBtnDelete="true"
          />
        ),
      },
    ];
    const { reload, paramSearchClass } = this.state;
    return (
      <Card title="Nhập khen thưởng">
        <Row>
          <Col span={24}>
            <FormFilterReward />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getListClassReward}
              paramSearch={paramSearchClass}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ClassRewardPage />
  </Page>
);



