import React, { Component } from 'react';
import { Col, Row, Form } from 'antd';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import { BtnSearch } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';
import BtnAddNew from 'components/common/button/BtnAddNew';
import './style.scss';

@Form.create({ name: 'formTeacher' })
class FormFilterReward extends Component {
  state = {};
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      updateQueryParams(values);
    });
  };
  render() {
    // const { getFieldDecorator } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khóa',
          },
        ],
      },
      studyFieldId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn CN',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
          <div className="btn-group">
            <BtnSearch htmlType="submit" />
          </div>
        </Row>
        <Row>
          <Col xs={24} sm={12} md={6}>
            <Link to={routeConstant.KHUYEN_THUONG__NHAP_QDKT_LOP__NHAP_DU_LIEU}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormFilterReward;



